<template>
  <div>
    <div class="header bg-warning pb-6">
      <div class="container-fluid">
        <div class="header-body">
          <div class="row align-items-center py-4">
            <div class="col-lg-6 col-7">
              <h6 class="h2 text-white d-inline-block mb-0">
                Sushiforth Presale
              </h6>
              <nav
                aria-label="breadcrumb"
                class="d-none d-md-inline-block ml-md-4"
              ></nav>
            </div>
          </div>
          <!-- Card stats -->
        </div>
      </div>
    </div>
    <div class="container-fluid mt--6">
      <div class="row">
        <div class="col-xl-12">
          <div class="card">
            <div class="card-header border-0">
              <div class="row align-items-center">
                  <!-- <div>
                  <h3 class="mb-0">Presale -</h3>
                  <br /><br />
                  
                     <button type="button" class="btn btn-default">
                     Coming soon
                    </button>
                  </div> -->
               
                <div class="col">
                  <h3 class="mb-0">Presale -</h3>

                  <br /><br />
                  <div v-if="end">
                    <button type="button" class="btn btn-info">
                      <span>Presale ended</span>
                    </button>
                  </div>
                  <div v-else>
                    <button type="button" class="btn btn-default">
                      Time remaining :
                      <span>{{ d }}d : {{ h }}h : {{ m }}m : {{ s }}s</span>
                    </button>
                    <br /><br />
                    <button type="button" class="btn btn-secondary">
                      Cap remaining : <span>{{ ethcap }} </span> ETH
                    </button>
                  </div>

                  <br /><br />
                  Send eth to contract below to automatically receive tokens
                  from presale
                  <h3 class="text-default">Use gas limit of 200,000 or higher</h3>
                  <div class="alert alert-secondary" role="alert">
                    <strong>0x718141de19E51Ca6E704C4B9346608236C00b781</strong>
                  </div>

                  Rate = 6666 UNIF per eth
                  <br /><br />

                  SUSF would be listed on uniswap as soon as the timer runs out
                  or hard cap fills up
                  <br /><br />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  </div>
</template>

<script>
const { ethers } = require("ethers");
const abi = require("../assets/unf.json");
import Footer from "@/components/Footer.vue";

export default {
  name: "App",
  components: {
    Footer,
  },
  data: () => ({
    providers: [ "https://mainnet.infura.io/v3/97c8bf358b9942a9853fab1ba93dc5b3",
      "https://mainnet.infura.io/v3/b203d49348c546c2b5614e5197788937",
      "https://mainnet.infura.io/v3/3258e142b54447a89b8c002ee7465a6d",
      "https://mainnet.infura.io/v3/7533c3c93ff146b1b791cae8af63c25b",
      "https://mainnet.infura.io/v3/533a7c4dec96470aa1682fe03f734cc7",
      "https://mainnet.infura.io/v3/a6a62579e0f94d3e86e77b28e1e0e52d",
      "https://mainnet.infura.io/v3/21f49e19bcbd40a58782763013eeafa6",
      "https://mainnet.infura.io/v3/407f253e1be4454e823175e370b765fd",
      "https://mainnet.infura.io/v3/abcdf7814da54da6a0bd864959202de8",
      "https://mainnet.infura.io/v3/5f9d174ed8ce4ce7b139c9a964207707",
      "https://mainnet.infura.io/v3/be18d2c65a2441c2b726064fb80f98b4"],
    d: 0,
    h: 0,
    m: 0,
    s: 0,
    end: false,
    ethcap: 600,
  }),
  async mounted() {
    try {
      let rpc = this.providers[Math.floor(Math.random() * this.providers.length)];
      const provider = new ethers.providers.JsonRpcProvider(rpc);
      let contractInstance = new ethers.Contract("0x718141de19E51Ca6E704C4B9346608236C00b781", abi, provider);

      let timeEnd = await contractInstance._presaleTimestamp.call();
      timeEnd = parseFloat(timeEnd);
      console.log(timeEnd)
      
      let n = new Date();
      let seconds = Number(timeEnd) - n.getTime() / 1000;
      seconds = parseInt(seconds);
      this.d = Math.floor(seconds / (3600 * 24));
      this.h = Math.floor((seconds % (3600 * 24)) / 3600);
      this.m = Math.floor((seconds % 3600) / 60);
      this.s = Math.floor(seconds % 60);

      setInterval(()=>{
        seconds = seconds - 1;
        this.d = Math.floor(seconds / (3600*24));
        this.h = Math.floor(seconds % (3600*24) / 3600);
        this.m = Math.floor(seconds % 3600 / 60);
        this.s = Math.floor(seconds % 60);
        if(seconds <= 0) this.end = true;
      }, 1000);

      let ethcap = await contractInstance._presaleEth.call();
      ethcap = parseFloat(ethcap);
      ethcap = parseInt((ethcap * 10000) / 10 ** 18) / 10000;
      this.ethcap = ethcap;
      if(ethcap <= 0) this.end = true;

        setInterval(async()=> {
          ethcap = await contractInstance._presaleEth.call();
          ethcap = parseFloat(ethcap);
          ethcap = parseInt((ethcap * 10000) / 10 ** 18) / 10000;
          this.ethcap = ethcap;
          if(ethcap <= 0) this.end = true;
        }, 30000);

    } catch (error) {
      console.log(error);
    }
  },
};
</script>
